.mm{
    padding: 40px 100px;

    display: flex;
    align-items: center;
    height: 80%;
}

.text{
    width: 50%;
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
}
.img{
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

h1{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 64px;
    margin: 0px;
    color: #2B2E39;
}
h3{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    text-align: left;

    color: #535353;
    width: 60%;
}