.box{
    width: 81%;
    height: 170px;

    background: #D9D9D9;
    border-radius: 14px;

    display: flex;
    align-items: center;

    padding: 0px 50px;
    gap: 4rem;
}

.intp-code{
    width: 60%;
    height: 72%;

    background: #FBFBFB;
    border-radius: 12px;

    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 600;
    font-size: 32px;
    line-height: 43px;
    /* identical to box height */

    display: flex;
    align-items: center;
    text-align: left;
    padding-left: 45px;

    
}

.intp-code::placeholder{
    color: #888888;
}

.btn-code{
    width: 26%;
    height: 70%;
    font-size: 1.7rem;
    background: rgba(113, 221, 163, 0.88);
    border-radius: 63px;
}