table {
    border-collapse: collapse;
    width: 100%;
    
    font-family: 'Lato';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    /* identical to box height */
    border-radius: 10px;
    overflow: hidden;
}
  
th, td {
    text-align: left;
    padding: 8px;
}

th {
    background-color: #333;
    color: white; 
}

  
tr:nth-child(even) {
    background-color: #f2f2f2;
}
  
tr:nth-child(odd) {
    background-color: #ffffff;
}

