.manager{
    display: flex;
    height: 100%;
    width: 100%;
}
.manager .conteiner > h1{
    font-family: 'Lato';
    font-style: normal;
    font-weight: 600;
    font-size: 38px;
    line-height: 58px;
    /* identical to box height */

    display: flex;
    align-items: center;
    text-align: left;
    width: 100%;
    margin-left: 00px;

    color: #000000;
    margin-bottom: 20px;
}
.btn-cadastrar{
    background: #7EDCA9;
    border-radius: 14px;
    height: 102px;
    transition: 0.3s;
}

.btn-cadastrar:hover{
    background: #50df90;
}

