header{
    height: 50px;

    padding: 15px 25px;

    display: flex;
    align-items: center;
    justify-content: space-between;
}
svg{
    width: 400px;
}
nav{
    height: 100%;
    width: 65%;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    font-family: "Ubuntu";
    font-size: 1.2rem;
    font-style: normal;
    font-weight: 500;
    gap: 35px;
}

nav > a{
    text-decoration: none;
    color: black;
    transition: 0.3s;
}
nav > a:hover{
    color: #314b9b;
}


nav > div{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0px 10px;

    width: 280px;
    height: 100%;

    background: #426ae38a;
    border-radius: 8px;
}

nav > div a {
    text-decoration: none;
}

nav > div > span{
    width: 50%;
    display: grid;
    place-items: center;
    cursor: pointer;
}

nav > div > span a{
    color: black;
    transition: 0.3s;
}

nav > div > span a:hover{
    color: #1c2a55;
    font-size: 1.3rem;
}

nav > div > div{
    width: 50%;
    height: 75%;
    background: #426AE3;
    border-radius: 8px;

    display: grid;
    place-items: center;
    cursor: pointer;
    transition: 0.3s;
}

nav > div > div a{
    color: white;
}

nav > div > div:hover{
    transform: scale(1.05);
    background: #426AE3;
}