.aside{
    height: 100%;
    width: 310px;

    background: #333333;
    

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.icon-logo{
    width: 46px;
    height: 46px;
}
.icon{
    display:unset;
    width: 33px;
    height: 33px;
}
.aside h1{
    font-size: 2.5rem;
    color: white;

    font-family: 'Lato';
    font-style: normal;
    font-weight: 600;
    font-size: 40px;
    line-height: 48px;
    display: flex;
    align-items: center;

    color: #FFFFFF;
}

.tittle{
    margin-top: 30px;
    display: flex;
    align-items: center;
    gap: 20px;
}

.safekids > a{
    text-decoration: none;
    width: fit-content;
}

.list{
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    gap: 25px;
}

.a-link{
    display: flex;
    align-items: center;
    /* justify-content: center; */
    text-decoration: none;
    gap: 24px;
}

.named{
    text-decoration: none;
    font-family: 'Lato';
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 29px;
    /* identical to box height */

    margin: 0px;

    color: #9A9A9A;
}

.atual{
    color: #819DD3;
}

.atual > path{
    fill: #819DD3;
}