input{
    border: none;
    outline: none;

    padding-left: 25px;

    width: 60%;
    height: 60px;

    background: rgba(194, 198, 209, 0.54);
    border-radius: 8px;


    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 27px;

    color: #414141;
    transition: 0s;
}

input::placeholder {
  color: #A6A6A6;
}

input:focus{
    border: 2px solid #0084ff;
    
}