.mainLogin{
    width: 100%;
    height: 100%;
    display: flex;
    padding: 0px;
}

.esq{
    width: 35%; 
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    gap: 2.1rem;
}

h2{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 36px;
    line-height: 49px;
    text-align: center;
    /* identical to box height */


    color: #000000;

}

form{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    gap: 1rem;
}

button{
    border: none;
    outline: none;

    width: 45%;
    height: 60px;
    background: #426AE3;
    border-radius: 8px;


    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 33px;
    color: #FFFFFF;
    transition: 0.3s;
}

button:hover{
    transform: scale(1.04);
    background: #29428f;
}

button:active{
    transform: scale(0.5);
    background: #29428f;
}

.external{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.external a{
    text-decoration: none;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    /* identical to box height */
    color: #0075FF;
    transition: 0.3s;
}

span a:hover{
    color: #0351aa;
    font-size: 15px;
}

.dir{
   width: 65%; 
   height: 100%;
}

.dir img{
    width: 100%;
    height: 100%;
    object-fit:cover;

    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 60px 0px 0px 60px;
}