.SF{
    width: 95%;
    height: 90px;

    background: #FBFBFB;
    border-radius: 12px;

    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 50px;
    
}

.SF h4{
    font-family: 'Lato';
    font-style: normal;
    font-weight: 600;
    font-size: 32px;
    line-height: 38px;
    display: flex;
    align-items: center;
    text-align: center;

    color: #000000;
}

.actions{
    display: flex;
    align-items: center;
    justify-content: flex-end;

    gap: 30px;
}

.actions button{
    width: 120px;
    height: 90px;
    background: #FFB5B5;
border-radius: 0px 12px 12px 0px;
}

.icons{
    width: 43px;
    height: 43px;

    cursor: pointer;
}